@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

.link {
  cursor: pointer; }

body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "PT Mono", arial, helvetica, sans-serif; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%); }

svg:not(:root) {
  overflow: hidden; }

svg,
svg * {
  fill: inherit;
  stroke: inherit; }

::-webkit-input-placeholder {
  /* Chrome */
  color: #ffffff; }

:-ms-input-placeholder {
  /* IE 10+ */
  color: #ffffff; }

::-moz-placeholder {
  /* Firefox 19+ */
  color: #ffffff;
  opacity: 1; }

:-moz-placeholder {
  /* Firefox 4 - 18 */
  color: #ffffff;
  opacity: 1; }

.header {
  background-color: #1b2a31; }

.header__logo-title,
.header__btn,
.header__title,
.header__btn-name {
  font-family: "Roboto Condensed", arial, helvetica, sans-serif;
  color: #ffffff; }

.header__btn {
  background-color: rgba(100, 100, 100, 0);
  border: 1px solid #ffffff; }

.header__logo-text {
  font-family: "Roboto", arial, helvetica, sans-serif;
  color: #788891; }

.header__mail,
.header__tel,
.header__text,
.header__information-scroll {
  font-family: "PT Mono", arial, helvetica, sans-serif;
  color: #ffffff; }

.header__information-btn {
  background-color: #00b2ff;
  border: none; }

.header__information-scroll {
  opacity: 0.5; }

.questions {
  background-color: #00b2ff; }

.questions__title,
.questions__btn {
  font-family: "Roboto Condensed", arial, helvetica, sans-serif;
  color: #ffffff; }

.questions__text,
.questions__input,
.questions__checkbox {
  font-family: "PT Mono", arial, helvetica, sans-serif;
  color: #ffffff; }

.questions__input,
.questions__textarea {
  background-color: rgba(255, 255, 255, 0.1);
  border: none; }

.questions__img {
  display: none; }

.questions__btn {
  border: 1px solid #ffffff;
  background-color: #00b2ff; }

.guidelines {
  background-color: #ffffff; }

.guidelines__title {
  font-family: "Roboto Condensed", arial, helvetica, sans-serif;
  color: #00b2ff; }

.guidelines__text {
  font-family: "PT Mono", arial, helvetica, sans-serif;
  color: #666666; }

.about-company {
  background-color: #ffffff; }

.about-company__title {
  font-family: "Roboto Condensed", arial, helvetica, sans-serif;
  color: #666666; }

.about-company__text {
  font-family: "PT Mono", arial, helvetica, sans-serif;
  color: #666666; }

.about-company__link {
  font-family: "Roboto Condensed", arial, helvetica, sans-serif;
  border: 1px solid #00b2ff;
  color: #00b2ff;
  background-color: #ffffff; }

.catalog {
  background-color: #1b2a31; }

.catalog__title,
.catalog__text {
  font-family: "Roboto Condensed", arial, helvetica, sans-serif;
  color: #ffffff; }

.footer {
  background-color: #fafafa; }

.footer__logo-title {
  font-family: "Roboto Condensed", arial, helvetica, sans-serif;
  color: #203c42; }

.footer__logo-text {
  font-family: "Roboto", arial, helvetica, sans-serif;
  color: #788891; }

.footer__sections-title {
  font-family: "Roboto Condensed", arial, helvetica, sans-serif;
  color: #1b2d37; }

.footer__sections-link,
.footer__contacts-text,
.footer__tel-link,
.footer__wrap-text {
  font-family: "PT Mono", arial, helvetica, sans-serif;
  color: #1b2d37; }
